import { connect } from "react-redux";
import { createAction as VzCreateAction } from "vizzuality-redux-tools"; // This is the namespace of the widget editor
// We are namespacing the widget editor to avoid data conflicts

export var APP_NAMESPACE = "widgetEditor";
/*
  REDUX util to connect to our state

  As the widget editor is namespaced in the consumer app
  this will make it easier to develop for us and only care about the state of the widget editor
*/

export var connectState = function connectState(stateHandler, dispatchHandler) {
  return connect(function (state) {
    return typeof stateHandler === "function" ? stateHandler(state[APP_NAMESPACE]) : {};
  }, dispatchHandler);
}; // Create redux actions that namespaces the widget editor

